import React, { Component } from 'react';
import {
    Button,
    Card,
    CardHeader,
    CardBody,
    Row,
    Col,
    Container,
    FormGroup,
    Label,
    CardImg
} from "reactstrap";
import _ from "lodash";
import { Link } from "react-router-dom";
import adminApi from '../../../api/adminApi';
import { toast } from 'react-toastify';

import { FormInput } from 'components/Form';
import ErrorSummary from 'components/ErrorSummary';

import './Account.scss';
import './AddAccount.scss';
import defaultAvatar from '../../../assets/img/theme/default-avatar.png';

const misMatchPasswordMessage = "Password does not match";

class AddAccount extends Component {
    state = {
        firstName: '',
        lastName: '',
        email: '',
        password: '',
        confirmPassword: '',
        errors: {},
        isFormReady: {},
        avatar: null,
    };

    handleChange = e => {
        const { name, value } = e.target;
        delete this.state.errors[name];
        delete this.state.errors["nonFieldErrors"];
        this.setState({
            [name]: value
        },
            () => {
                if (name === 'confirmPassword' || name === "password") {
                    const { password, confirmPassword } = this.state;
                    if (password && confirmPassword && password !== confirmPassword) {
                        this.setState({ errors: { ...this.state.errors, confirmPassword: [misMatchPasswordMessage] } })
                    } else {
                        const tempError = this.state.errors;
                        delete tempError['confirmPassword'];
                        this.setState({ errors: tempError });
                    }
                }
            }
        )
    }

    handleFile = (event) => {
        const { name, files } = event.target;
        if (files.length) {
            this.setState({
                [name]: files[0]
            })
        }
    }

    handleError = (field, status) => {
        let check = true;
        if (status) {
            for (let key in this.state.isFormReady) {
                check = check && this.state.isFormReady[key];
            }
        }
        this.setState(
            { isFormReady: { ...this.state.isFormReady, [field]: status, check: check && status } }
        );
    }

    addAdminAccount = e => {
        e.preventDefault();
        const { firstName, lastName, email, password, avatar } = this.state;
        const payload = { firstName, lastName, email, password, avatar }
        if (_.isNull(payload.avatar)) {
            delete payload['avatar'];
        }
        adminApi.addAdminAccount(payload).then(() => {
            this.setState({ errors: {} });
            toast.success("Create Account success");
            this.goToSystemPage();
        }).catch(err => {
            const errors = err.response.data;
            this.setState({ errors });
            toast.error("Create Account fail");
        })
    }

    goToSystemPage = () => {
        this.props.history.push("/admin/system")
    }

    render() {
        const { errors, avatar } = this.state
        return (
            <>
                <Container className="mt-6 d-flex justify-content-center align-items-center add-account" fluid >
                    <Card className="card-profile card-custom">
                        <Row className="justify-content-center card-profile-image">
                            <div onClick={() => this.openImageFile.click()}>
                                <CardImg
                                    alt="..."
                                    className="rounded-circle avatar custom-avatar"
                                    src={_.isObject(avatar) ? URL.createObjectURL(avatar) : defaultAvatar}
                                />
                            </div>
                        </Row>
                        <CardHeader className="text-center border-0 pb-0">
                            <Row className="justify-content-center mt-4">
                                <Col>
                                    <input
                                        ref={(ref) => this.openImageFile = ref}
                                        onChange={this.handleFile}
                                        className="d-none"
                                        type="file"
                                        name="avatar"
                                        id="roomImage"
                                        accept=".jpg,.gif,.jpeg,.png" />
                                    <Button
                                        className="mt-4 align-items-center"
                                        color="link"
                                        onClick={() => this.openImageFile.click()}
                                    >
                                        Select Image
                                    </Button>
                                    <div className="error-text mt-3">{errors['image']}</div>
                                </Col>
                            </Row>
                        </CardHeader>
                        <CardBody className="border-0 pb-0 mx-3 wrapper__body">
                            <FormGroup>
                                <Row>
                                    <Label className="form-control-label" >
                                        First Name
                                        <sup className="text-required">*</sup>
                                    </Label>
                                </Row>
                                <Row className="d-block">
                                    <FormInput
                                        type="text"
                                        name="firstName"
                                        handleError={this.handleError}
                                        errors={errors['firstName']}
                                        onChange={this.handleChange}
                                        required={true}
                                    />
                                </Row>
                            </FormGroup>
                            <FormGroup>
                                <Row>
                                    <Label className="form-control-label" >
                                        Last Name
                                        <sup className="text-required">*</sup>
                                    </Label>
                                </Row>
                                <Row className="d-block">
                                    <FormInput
                                        type="text"
                                        name="lastName"
                                        handleError={this.handleError}
                                        errors={errors['lastName']}
                                        onChange={this.handleChange}
                                        required={true} />
                                </Row>
                            </FormGroup>
                            <FormGroup>
                                <Row>
                                    <Label className="form-control-label" >
                                        E-mail
                                        <sup className="text-required">*</sup>
                                    </Label>
                                </Row>
                                <Row className="d-block">
                                    <FormInput
                                        type="email"
                                        name="email"
                                        handleError={this.handleError}
                                        errors={errors['email']}
                                        onChange={this.handleChange}
                                        regex="email"
                                        required={true} />
                                </Row>
                            </FormGroup>
                            <FormGroup>
                                <Row>
                                    <Label className="form-control-label" >
                                        Password
                                        <sup className="text-required">*</sup>
                                    </Label>
                                </Row>
                                <Row className="d-block">
                                    <FormInput
                                        type="password"
                                        name="password"
                                        handleError={this.handleError}
                                        errors={errors['password']}
                                        onChange={this.handleChange}
                                        minLength={8}
                                        regex="password"
                                        required={true}
                                    />
                                </Row>
                            </FormGroup>
                            <FormGroup>
                                <Row>
                                    <Label className="form-control-label" >
                                        Confirm Password
                                        <sup className="text-required">*</sup>
                                    </Label>
                                </Row>
                                <Row className="d-block">
                                    <FormInput
                                        type="password"
                                        name="confirmPassword"
                                        handleError={this.handleError}
                                        errors={errors['confirmPassword']}
                                        onChange={this.handleChange}
                                        required={true}
                                    />
                                </Row>
                            </FormGroup>
                            <ErrorSummary error={errors} className="mt-3" />
                        </CardBody>
                        <CardBody className="pb-md-4">
                            <Row>
                                <Col className="mt-3 d-flex justify-content-center">
                                    <div className="text-center w-75">
                                        <Link to={`/admin/system`} className="d-block w-100 mb-3">
                                            <Button color="link" className="w-100">CANCEL</Button>
                                        </Link>
                                        <Button
                                            color="success"
                                            onClick={this.addAdminAccount}
                                            // disabled={!formValid}
                                            className="w-100"
                                        >
                                            Save Changes
                                        </Button>
                                    </div>
                                </Col>
                            </Row>
                        </CardBody>
                    </Card>
                </Container>
            </>
        );
    }
}

export default AddAccount;
